.loader-wrapper {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background: #f9fafb;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loaded .loader-wrapper {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all 0.8s ease-out;
  transition: all 0.8s ease-out;
}
.loader-wrapper .loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #727cf5;
  background: -moz-linear-gradient(left, #727cf5 10%, rgba(0, 0, 0, 0) 42%);
  background: -webkit-linear-gradient(left, #727cf5 10%, rgba(0, 0, 0, 0) 42%);
  background: -o-linear-gradient(left, #727cf5 10%, rgba(0, 0, 0, 0) 42%);
  background: -ms-linear-gradient(left, #727cf5 10%, rgba(0, 0, 0, 0) 42%);
  background: linear-gradient(to right, #727cf5 10%, rgba(0, 0, 0, 0) 42%);
  -webkit-animation: loading 1.4s infinite linear;
  animation: loading 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
.loader-wrapper .loader.loader:before {
  width: 50%;
  height: 50%;
  background: #727cf5;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
}
.loader-wrapper .loader.loader:after {
  background: #f9fafb;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: "";
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.main-wrapper {
  display: flex;
  overflow: hidden;
}
.main-wrapper .page-wrapper {
  min-height: 100vh;
  background: #f9fafb;
  width: calc(100% - 240px);
  margin-left: 240px;
  display: flex;
  flex-direction: column;
  -webkit-transition: margin 0.1s ease, width 0.1s ease;
  transition: margin 0.1s ease, width 0.1s ease;
}
.main-wrapper .page-wrapper .page-content {
  flex-grow: 1;
  padding: 25px;
  margin-top: 60px;
}
@media (max-width: 767px) {
  .main-wrapper .page-wrapper .page-content {
    padding: 25px 15px;
